<script>
export default {
  data() {
    return {
      link: ["", "", "", "", "", ""],
      imgdel: null,
    };
  },
  mounted() {
    this.imgWidth();
  },
  methods: {
    imgWidth() {
      setInterval(() => {
        let w = window.innerWidth;
        if (w <= 778) {
          this.imgdel = false;
        } else {
          this.imgdel = true;
        }
        if (w <= 366) {
          this.imgdel = true;
        }
      }, 100);
    },
  },
};
</script>

